import { useEffect, useState } from "react";

interface LotteryTextProps {
  text: string
}

export const funnyWeddingTableNames: string[] = [
  "Stolik Singli", "Teciowie w Szoku", "Ekipa Od Toastw", "Kuzyni z Daleka", "Przyjaciele Panny Modej",
  "Przyjaciele Pana Modego", "Zgrana Paczka", "Sto Lat Bez Koca", "Najlepsi Tancerze", "Pogromcy Tortu",
  "mietanka Towarzyska", "Elita Wesela", "Koneserzy Wdki", "Testosteron Team", "Damy i Dentelmeni",
  "Najgoniejszy St", "Krlowie Parkietu", "Pacimy za Wesele", "Kontrola Rodzicielska", "Cicha Woda",
  "Ekipa Od Selfie", "Delegacja Firmowa", "Wujkowie Specjalici", "Wesele Roku", "Stolik Mistrzw",
  "Nie Pijemy (art)", "Tylko Na Wesele", "Bez Litoci", "Grupa Wsparcia", "Sekta Wujka Staszka"
];

export const LotteryText = ({ text }: LotteryTextProps) => {
    const [displayedText, setDisplayedText] = useState('');
    const [revealed, setRevealed] = useState(false);
  
    useEffect(() => {
      let interval: NodeJS.Timeout;
      let timeout: NodeJS.Timeout;
  
      if (!revealed) {
        interval = setInterval(() => {
          const randomWord = Math.floor(Math.random() * funnyWeddingTableNames.length)
          setDisplayedText(funnyWeddingTableNames[randomWord]);
        }, 100);
        
        timeout = setTimeout(() => {
          clearInterval(interval);
          setDisplayedText(text);
          setRevealed(true);
        }, 1000);
      }
  
      return () => {
        clearInterval(interval);
        clearTimeout(timeout);
      };
    }, [text, revealed]);
  
    return <span>{displayedText}</span>;
  };